import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/pages/HomePage'
import AboutMe from '@/pages/AboutMe'
import Blog from '@/pages/Blog/Blog'
import B1 from '@/pages/Blog/B1'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
    path: '/',
    component: HomePage
  },
  {
    path: '/aboutme',
    component: AboutMe
  },
  {
    path: '/blog',
    component: Blog
  },
  {
    path: '/B1',
    component: B1
  }]
})



